import React, { Component } from "react";
import { authHeader } from "../helpers/authHeader";
import appSettings from "../helpers/appSetting";
import axios from "axios";
import "../styles/custom.css";
import "../assets/css/react-table.css";
import { Button, Modal, ModalBody } from "reactstrap";
import Headers from "../component/header";
import SideMenu from "../component/sidemenu";
import Eye from "./../assets/img/eye.png";
import Copy from "./../assets/img/copy.png";
import Edit from "./../assets/img/pencil.png";
import matchSorter from "match-sorter";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { encryption } from "../helpers/encryption";

class BookingTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalDel: false,
      filterAll: "",
      bookingData: []
    };
    this.HandleBookingList = this.HandleBookingList.bind(this);
    this.toggleDel = this.toggleDel.bind(this);
    this.filterAll = this.filterAll.bind(this);
    this.onFilteredChange = this.onFilteredChange.bind(this);
  }

  toggleDel() {
    this.setState(prevState => ({
      modalDel: !prevState.modalDel
    }));
  }

  componentDidMount() {
    this.HandleBookingList();
  }

  HandleBookingList() {
    let self = this;
    var userid = encryption(window.localStorage.getItem("userid"), "desc");

    axios({
      method: "post",
      url: `${appSettings.APIURL}/BookingGridAPI`,
      data: {
        UserId: userid
      },
      headers: authHeader()
    }).then(function(response) {
      debugger;
      var data = [];
      var pending = 0,
        approved = 0,
        rejected = 0;
      data = response.data;
      if (data.length > 0) {
        self.setState({ bookingData: data }); ///problem not working setstat undefined
      }
      for (let i = 0; i < data.length; i++) {
        if (data[i].Status === "Approved") {
          approved = approved + 1;
        } else if (data[i].Status === "Pending") {
          pending = pending + 1;
        } else if (data[i].Status === "Rejected") {
          rejected = rejected + 1;
        }
      }
      window.localStorage.setItem("bookpending", pending);
      window.localStorage.setItem("bookapproved", approved);
      window.localStorage.setItem("bookrejected", rejected);
    });
  }

  HandleChangeShipmentDetails(BookingNo) {
    debugger;

    this.props.history.push({
      pathname: "rate-finalizing-still-booking",
      state: { detail: BookingNo }
    });
  }
  onFilteredChange(filtered) {
    if (filtered.length > 1 && this.state.filterAll.length) {
      const filterAll = "";
      this.setState({
        filtered: filtered.filter(item => item.id != "all"),
        filterAll
      });
    } else this.setState({ filtered });
  }

  filterAll(e) {
    const { value } = e.target;
    const filterAll = value;
    const filtered = [{ id: "all", value: filterAll }];

    this.setState({ filterAll, filtered });
  }
  HandleCopyClick(evt, row) {
    debugger;
    var BookingNo = row.original["BookingID"];
    this.props.history.push({
      pathname: "rate-finalizing-still-booking",
      state: { BookingNo: BookingNo, Copy: true }
    });
  }

  HandleRowClickEvt(evt, row) {
    debugger;
    var BookingNo = row.original["BookingID"];
    this.props.history.push({
      pathname: "rate-finalizing-still-booking",
      state: { BookingNo: BookingNo ,isView:true}
    });
  }
  HandleDocumentView(evt, row) {
    debugger;
    var BookingNo = row.original["BookingID"];
    this.props.history.push({
      pathname: "rate-finalizing-still-booking",
      state: { BookingNo: BookingNo }
    });
  }
  render() {
    const { bookingData } = this.state;
    return (
      <div>
        <Headers />
        <div className="cls-ofl">
          <div className="cls-flside">
            <SideMenu />
          </div>
          <div className="cls-rt">
            <div className="title-sect">
              <h2>Booking Table</h2>
            </div>
            <div className="">
              <input
                type="search"
                className="quote-txt-srch"
                placeholder="Search here"
                value={this.state.filterAll}
                onChange={this.filterAll}
              />
            </div>
            <div className="ag-fresh">
              <ReactTable
                data={bookingData}
                noDataText=""
                onFilteredChange={this.onFilteredChange.bind(this)}
                filtered={this.state.filtered}
                defaultFilterMethod={(filter, row) =>
                  String(row[filter.id]) === filter.value
                }
                columns={[
                  {
                    columns: [
                      {
                        Header: "No.",
                        accessor: "BookingNo"
                      },
                      {
                        Header: "Consignee",
                        accessor: "Consignee_Name"
                      },
                      {
                        Header: "Shipper",
                        accessor: "Shipper_Name",
                        filterable: true
                      },
                      {
                        Header: "POL",
                        accessor: "POL"
                      },
                      {
                        Header: "POD",
                        accessor: "POD"
                      },
                      {
                        Header: "Commodity",
                        accessor: "Commodity"
                      },
                      {
                        Header: "Status",
                        accessor: "Status"
                      },
                      {
                        Header: "Action",
                        sortable: false,
                        Cell: row => {
                          if (row.original.Status === "Pending") {
                            return (
                              <div className="action-cntr">
                                {/* <a> */}
                                {/* <img
                            className="actionicon"
                            src={Eye}
                            alt="view-icon"
                            onClick={e => this.HandleRowClickEvt(e, row)}
                          /> */}
                                {/* </a> */}
                                {/* <a href="/rate-finalizing-still-booking"> */}
                                <img
                                  className="actionicon"
                                  src={Edit}
                                  alt="view-icon"
                                  onClick={e => this.HandleDocumentView(e, row)}
                                />
                                {/* </a> */}

                                <img
                                  className="actionicon"
                                  src={Copy}
                                  alt="view-icon"
                                  onClick={e => this.HandleCopyClick(e, row)}
                                />
                              </div>
                            );
                          }
                          if (row.original.Status === "Approved") {
                            return (
                              <div className="action-cntr">
                                {/* <a> */}
                                {/* <img
                            className="actionicon"
                            src={Eye}
                            alt="view-icon"
                            onClick={e => this.HandleRowClickEvt(e, row)}
                          /> */}
                                {/* </a> */}
                                {/* <a href="/rate-finalizing-still-booking"> */}
                                <img
                                  className="actionicon"
                                  src={Edit}
                                  alt="view-icon"
                                  onClick={e => this.HandleDocumentView(e, row)}
                                />
                                {/* </a> */}

                                <img
                                  className="actionicon"
                                  src={Copy}
                                  alt="view-icon"
                                  onClick={e => this.HandleCopyClick(e, row)}
                                />
                              </div>
                            );
                          }
                          if (row.original.Status === "Rejected") {
                            return <></>;
                          }
                        }
                      }
                    ]
                  },
                  {
                    show: false,
                    Header: "All",
                    id: "all",
                    width: 0,
                    resizable: false,
                    sortable: false,
                    Filter: () => {},
                    getProps: () => {
                      return {
                        // style: { padding: "0px"}
                      };
                    },
                    filterMethod: (filter, rows) => {
                      const result = matchSorter(rows, filter.value, {
                        keys: [
                          "BookingNo",
                          "Consignee_Name",
                          "Shipper_Name",
                          "POL",
                          "POD",
                          "Commodity",
                          "Status"
                        ],
                        threshold: matchSorter.rankings.WORD_STARTS_WITH
                      });

                      return result;
                    },
                    filterAll: true
                  }
                ]}
                className="-striped -highlight"
                defaultPageSize={5}
                minRows={1}
                // getTrProps={this.HandleRowClickEvt}
              />
            </div>
          </div>
          <Modal
            className="delete-popup"
            isOpen={this.state.modalDel}
            toggle={this.toggleDel}
            centered={true}
          >
            <ModalBody>
              <p>Are you sure ?</p>
              <Button className="butn" onClick={this.toggleDel}>
                Yes
              </Button>
              <Button className="butn cancel-butn" onClick={this.toggleDel}>
                No
              </Button>
            </ModalBody>
          </Modal>
        </div>
      </div>
    );
  }
}
function transportMode(params) {
  var element = document.createElement("span");
  var imageElement = document.createElement("img");
  switch (params.value) {
    case "Air": {
      imageElement.src =
        "data:image/png;base64,";
      imageElement.style.width = "65px";
      element.append(imageElement);
      break;
    }
    case "Ocean": {
      imageElement.src =
        "data:image/png;base64,";
      imageElement.style.width = "65px";
      element.append(imageElement);
      break;
    }
    case "Truck": {
      imageElement.src =
        "data:image/png;base64,";
      imageElement.style.width = "65px";
      element.append(imageElement);
      break;
    }
    case "rail": {
      imageElement.src =
        "data:image/png;base64,";
      imageElement.style.width = "65px";
      element.append(imageElement);
      break;
    }
  }
  return element;
}

function statusImage(parameter) {
  var element = document.createElement("span");
  var imageElement = document.createElement("img");
  switch (parameter.value) {
    case "Departed": {
      //delivered
      imageElement.src =
        "data:image/png;base64,";
      imageElement.style.width = "45px";
      element.append(imageElement);
      break;
    }
    case "Transshipped": {
      //box
      imageElement.src =
        "data:image/png;base64,";
      imageElement.style.width = "45px";
      element.append(imageElement);
      break;
    }
    case "Planning in Progress": {
      //transit
      imageElement.src =
        "data:image/png;base64,"; //transit
      imageElement.style.width = "45px";
      element.append(imageElement);
      break;
    }
    case "Arrived": {
      //plane
      imageElement.src =
        "data:image/png;base64,"; //in-plane
      imageElement.style.width = "45px";
      element.append(imageElement);
      break;
    }
  }
  return element;
}
function etaDate(params) {
  if (params.value != undefined && params.value != "") {
    return formatDate(params.value);
  }
}
function formatDate(date) {
  var monthNames = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December"
  ];
  debugger;
  var day = date.getDate();
  var monthIndex = date.getMonth();
  var year = date.getFullYear();

  return day + " /" + monthIndex + "/" + year;
}

function formatNumber(number) {
  return Math.floor(number)
    .toString()
    .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}
export default BookingTable;
